video {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    transform: translateX(calc((100% - 100vw) / 2));

    @media (max-width: 1300px) {
        transform: translateX(calc((100% - 100vw) / 3));
    }

    @media (max-width: 1000px) {
        transform: translateX(calc((100% - 100vw) / 4));
    }

    @media (max-height: 800px) {
        right: -250px;
        top: 0;
        transform: translateY(calc((50% - 100vh)));
    }

    @media (max-height: 720px) {
        right: -400px;
        top: 0;
        transform: translateY(calc((40% - 100vh)));
    }
}
